body, html{
  color: #000;
  font-family:Arial, Helvetica, sans-serif;
  text-align: left;
  font-size: 14px;

  height: 100%;
  margin: 0;
}

.bg {
  /* The image used */
 /* background-color: rgb(156, 197, 214);  */
 background-image: url("../public/background.jpg");
  /* Full height */
  height: 100%; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

p{
margin-block-start:  0.7em;
margin-block-end: 0.7em;
  }

  a:link {
    color: #000;
  }
  
  /* visited link */
  a:visited {
    color: green;
  }
  
  /* mouse over link */
  a:hover {
    color: #fff;;
  }
  
  /* selected link */
  a:active {
    color: #000;
  }

.cv_content{
  display: grid;
  grid-template-columns: 260px 700px;
  grid-auto-rows: minmax(150px, auto);
  max-width: 960px;
  margin: 0 auto;
}
.cv_content > *{
  padding: 10px;
}

.cv_header{
  grid-column: 2 / 2;			
  background: #00b8d7;
  color : white;
}

.cv_header_contact_info{
  grid-column: 1 / 2;			
  background: #00b8d7;
  

}
.cv_right{
  grid-column: 2 / 4;
  grid-row: 2 / 5;
  background: #FFF;

}

.cv_left{
  grid-column: 1 / 2;
  grid-row: 2 / 5;
  background: #00b8d7;
  color : white;
  text-align: left
}
.cv_footer{
  grid-column: span 3;
  background: #a56dda;
}

.circle_little {
  height: 20px;
  width: 20px;

  padding-left: 0px;
  padding-right: 0px;
  padding-top: 0px;
  padding-bottom: 0px;

  background-color: #fff;
  margin-right: 10px;;
  color: #000;
  text-align: center;
  border-radius: 50%;
  display: inline-block;
}

.icon_vertical_center {
  vertical-align: middle;
}

.section_title {  
  font-weight: 600;
  font-size: 17px;
}

.star_checked {
  color: #000;
}

.skill_entry_indent {
  text-indent: 20px;
}

.skill_entry_star_indent {
  text-indent: 10px;
}

.education_entries_indent {
  text-indent: 20px;
}

.education_entry_indent {
  text-indent: 30px;
}

.education_entry_title {
  text-indent: 20px;
  font-size: 15px;
  font-weight: 600;
}

.language_entries_indent {
  text-indent: 20px;
}

.language_entry_indent {
  text-indent: 30px;
}

.language_entry_title {
  text-indent: 20px;
  font-size: 15px;
  font-weight: 600;
}



.sparetime_entry_title {
  margin-left: 15px;
  font-size: 15px;
  line-height: '1.5'
}

.section_ruler {  
  border-top: 1px solid #000;
}

.working_history_entry_tofrom {
  margin-left: 15px;
}

.working_history {
   margin-left: 40px; 
   word-wrap: break-word;

}

.working_experience_content{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: minmax(20px, auto);
  max-width: 840px;
  margin-left: 27px; 

}

.working_experience_content_grid_item_right {
  align-items: center;
  justify-content: center;
  width: 450px;
}

.working_experience_content_grid_item_left {
  align-self: center;
  align-items: center;
  justify-content: center;
}

.working_experience_content_grid_item_achivements {
  grid-column: 1 / 3;	

}

.working_history_entry_title {
  margin-left: 10px;
  font-size: 15px;
  font-weight: 600;
}

.working_history_achivements_title {
  margin-left: 15px;
  font-size: 13px;
  font-weight: 600;
}

.working_history_achivements {
  margin-left: 12px;
  font-size: 15px;

}

.used_skill_tag-cloud {
  display: inline-block;
  color: white;
  padding: 5px 5px;
  font-family: Arial;
  border-radius: 30px;
  background-color: #00b8d7;
  margin-left: 8px;
  margin-bottom: 8px;
  font-size: 11px;
}

.span_inline_block {
  display:inline-block;
}

.span_icon_color {
  color: #80cdef;
}

